.nav{/* Rectangle 19333 */

    position: absolute;
    width: 1920px;
    height: 83px;
    left: calc(50% - 1920px/2);
    top: -1px;
    
    background: #FFFFFF;
    /* Shadow/Level1 */
    box-shadow: 0px 2px 8px rgba(6, 77, 173, 0.1);
    }
    .allink{/* Frame 1261158371 */

        /* Auto layout */
        display: flex;
        flex-direction: row;
        align-items: center;
        padding: 0px;
        gap: 32px;
        
        position: absolute;
        width: 332px;
        height: 21px;
        left: calc(50% - 332px/2);
        top: 33px;
        
        
        }
        .link.active {
  font-weight: bold;
  color: blue;
}